#main .thing h4 {
  font-size: 1.7rem;
  margin-top: 0;
  margin-bottom: 12px; }

#main .thing p {
  font-size: 1.2rem; }

#main .thing img {
  width: 3rem;
  height: 4rem;
  margin-right: 1rem;
  float: left; }

#main ul {
  list-style-type: disc; }
  #main ul li p {
    display: inline; }

#header #brand img {
  display: none; }

#header ul {
  margin: auto; }

#header.home #brand {
  display: none; }

#header.home > div {
  text-align: center; }

#header.home nav {
  float: none; }
  #header.home nav span.nav-name {
    display: inline-block; }

#doodles {
  margin: 0 auto;
  border: none; }
  #doodles td.next span {
    font-size: 2rem;
    min-width: 80px;
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    min-height: 80px;
    border-radius: 50%; }
    #doodles td.next span:hover {
      color: #268bd2; }
  #doodles td.prev span {
    font-size: 2rem;
    border-radius: 50%;
    padding: 10px 10px 10px 10px;
    cursor: pointer;
    min-width: 80px;
    min-height: 80px; }
    #doodles td.prev span:hover {
      color: #268bd2; }
  #doodles #doodle-gallery {
    list-style: none;
    padding: 0;
    margin: 0 auto; }
    #doodles #doodle-gallery li {
      text-align: center; }
      #doodles #doodle-gallery li span {
        display: block;
        margin: 5px; }
    #doodles #doodle-gallery .next,
    #doodles #doodle-gallery .prev {
      cursor: pointer;
      vertical-align: middle; }
    #doodles #doodle-gallery img {
      max-height: 95%;
      max-width: 95%; }

.imgbox {
  border: 1px solid silver;
  border-radius: 12px; }
